import React from 'react';

//Import React-Chrono
import { Chrono } from "react-chrono";

//Import Moment.js
import moment from 'moment';
import 'moment/locale/es';

const Gastronomia = () => {

    const INICIO = moment("2012-04-5");
    const HOY = moment();
    const duracion = moment.duration(HOY.diff(INICIO));

    const items = [
        
        {
            title: "Agosto 2019 - Julio 2020",
            cardTitle: "Propietario",
            cardSubtitle:"El Jbaro Coffee",
            cardDetailedText: ["Marca personal de manejo cultural del caf, ofrecia consultorias, desarrollo de productos, entrenamiento a caficultores, catacin y tostin de caf, todo esto en una tienda de cafe ubicada en el tradicional barrio San Antonio de Cali."]
        },

        {
            title: "Septiembre 2017",
            cardTitle: "Conferencista",
            cardSubtitle:"Bar Show Colombia",
            cardDetailedText: "Conferencia sobre el Caf Colombiano de especialidad."
        },

        {
            title: "Febrero 2018 - Septiembre 2019",
            cardTitle: "Tostador de caf y director de calidad.",
            cardSubtitle:"Caf del Huerto",
            cardDetailedText: "Manejo de inventarios, capacitaciones, tostion de caf, manejo de la calidad del caf en cultivo y eventos con consumidores."
        },

        {
            title: "Abril 2016 - Diciembre 2018",
            cardTitle: "Profesor de gastronomia",
            cardSubtitle:"Politcnico Empresarial Colombiano",
            cardDetailedText: "Profesor encargado de ensear cocteleria, cocina bsica y cocina internacional."
        },

        {
            title: "Agosto 2016 - Diciembre 2017",
            cardTitle: "Profesor de gastronomia",
            cardSubtitle:"IMETY - YUMBO",
            cardDetailedText: "Profesor encargado del rea de bebidas, servicio al cliente, etiqueta y protocolo y diplomados de cocina internacional."
        },

        {
            title: "Octubre 2014 - Mayo 2016",
            cardTitle: "Barista Jefe",
            cardSubtitle:"Caf Celeste",
            cardDetailedText: "Preparacin de bebidas en base a caf, bebidas y cocteles, manejo de inventarios, caja y coordinacin de horarios."
        },

        {
            title: "Agosto 2013 - Septiembre 2014",
            cardTitle: "Bartender",
            cardSubtitle:"Mansin del Rio",
            cardDetailedText: "Preparacin de bebidas, cocteles, cafes y manejo de inventarios."
        },

        {
            title: "Marzo 2013 - Mayo 2013",
            cardTitle: "Chef Asesor",
            cardSubtitle:"Restaurante Origami",
            cardDetailedText: "Montaje de carta de comida asiatica, desarrollo del men de bebidas y entrenamiento al personal."
        },

        {
            title: "Agosto 2012 - Diciembre 2012",
            cardTitle: "Chef Capacitador",
            cardSubtitle:"FoodServices de Colombia",
            cardDetailedText: "Estandarizacin de recetas y entrenamiento del equipo de cocina en los diferentes casinos que atendia la empresa."
        },

    ];


    return (
        <div>
            <div style={{ width: "100%", height: "100%", textAlign:"justify"}}>

                <div className="h5 text-center m-4 rounded-3 p-3" style={{border:'1px solid #F28705'}}>

                    Tiempo de experiencia: {duracion.years()} {duracion.years() > 1 ? " Aos" : " Ao"}, {duracion.months()}{duracion.months() > 1 ? " Meses" : " Mes"}, {duracion.days()} {duracion.days() < 1 ? " Da" : " Das"}

                </div>

                <Chrono 
                    items={items}
                    mode="VERTICAL" 
                    disableNavOnKey
                    hideControls
                    theme={{
                        primary: '#F28705',
                        secondary: '#DAD5CF',
                        cardBgColor: '#DAD5CF',
                        cardForeColor: '#24242E',
                        titleColor: '#DAD5CF',
                        titleColorActive: '#24242E',
                    }}
                    cardHeight="100%"
                    useReadMore={false}
                />
            </div>
        </div>
    )
}

export default Gastronomia
